import { createRouter, createWebHashHistory} from 'vue-router'
import AdminView from "@/views/admin/AdminView.vue"

const router = createRouter({
  // 修改
  // history: createWebHistory(import.meta.env.BASE_URL),
  history: createWebHashHistory(),

  routes: [
    {
      path: '/',
      redirect: { name: 'home' }
    },
    {
      path: '/admin',
      name: 'admin',
      component: AdminView, // () => import('@/views/AdminView.vue'),
      children :[
          {
            path: 'home',
            name: 'home',
            component: () => import('@/views/admin/HomeView.vue')
          },
          {
            path: 'modeDefinition',
            name: 'modeDefinition',
            component: () => import('@/views/admin/ModeDefinition.vue')              
          },
          {
            path: 'formDesigner',
            name: 'formDesigner',
            component: () => import('@/views/admin/FormDesigner.vue')              
          },
          {
            path: 'formCollection',
            name: 'formCollection',
            component: () => import('@/views/admin/FormCollection.vue')              
          },
          {
            path: 'dataCollection',
            name: 'dataCollection',
            component: () => import('@/views/admin/DataCollection.vue')              
          },
          {
            path: 'formFinish',
            name: 'formFinish',
            component: () => import('@/views/admin/FormFinish.vue')
          },
      ]
    },
    {
      path: '/user/submission/',
      name: 'publicSubmission',
      component: () => import('@/views/user/PublicSubmission.vue')
    },
    {
      path: '/user/submission/finish',
      name: 'submissionFinish',
      component: () => import('@/views/user/SubmissionFinish.vue')
    },
    { 
      path: '/:pathMatch(.*)*', 
      name: 'notFound',
      component: () => import('@/views/404.vue') 
    },

  ]
})

export default router